<template>
    <div class="PcbTop">
      <div class="Head" :class="PcbTopTitle.class==true?'bg_fff':''">
        <i class="iconfont pa left24" @click="prev"></i>
        {{PcbTopTitle.name}}
        <div class="iconfontRight" @click="messages">
          <i class="iconfont">&#xe60b;</i>
        </div>
      </div>
    </div>
</template>

<script>
    import Vue from "vue"

    export default {
        name: "PcbTop",
        props:["PcbTopTitle"],
        components: {},
        data() {
            return {
              GoHome:"",
            }
        },
        methods: {
          prev(){
            if(this.GoHome!=""){
              this.$router.push({
                path:this.GoHome
              })
            }else{
              this.$router.go(-1)
            }
            
            
            },
          messages(){this.$router.push({path: "SettingContent",})},
        },
        created() {
          if(this.PcbTopTitle.goHome!=undefined){
            this.GoHome=this.PcbTopTitle.goHome;
          }else{

          }
        }
    }
</script>



