<template>
	<div class="HomePageTop">
    <div class="shade"></div>
    <div class="Head" :class="TopTitle.class==true?'bg_fff':''">
      <i class="iconfont pa left24" @click="prev"></i>
      {{TopTitle.name}}
    </div>
   	</div>
</template>

<script>
	import Vue from "vue"
	export default {
		name: "HomePageTop",
    props:["TopTitle"],
		components: {

		},
		data() {
			return {
			  GoHome:"",
			}
		},
		methods: {
      prev(){
        if(this.GoHome!=""){
          this.$router.push({
            path:this.GoHome
          })
        }else{
          this.$router.go(-1)
        }

      },

		},
		created() {

      if(this.TopTitle.goHome!=undefined){
        this.GoHome=this.TopTitle.goHome;
      }else{

      }

		}
	}
</script>

<style>
</style>



